<template>
  <div style="padding-bottom:20px;background:#f1eff2;">
    <mu-appbar style="width: 100%;" color="#4ed995">
      <div slot="left" color="#3051f1">
        <a href="#/EventControlList">
          <mu-button icon slot="left" class="icon" size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">事件管理</span>
      </div>
      <div slot="right" class="icon" size="20">
        <a href="#/EventControl">
          <mu-button icon slot="left" class="icon" size="20">
            <mu-icon value="add" size="24" color="white"></mu-icon>
          </mu-button>
        </a>
      </div>
    </mu-appbar>
    <div class="contanerMain">
      <!-- <select name="" id=""></select> -->
      <div v-for="(item,index) in DataList" :key="index">
        <div class="eventDetailsDataList">
          <span style="color:#7a7a7a">事件编号</span>
          <span>{{item.name}}</span>
        </div>
        <div class="eventDetailsDataList">
          <span style="color:#7a7a7a">事件名称</span>
          <span>{{item.eventName}}</span>
        </div>
        <div class="eventDetailsDataList">
          <span style="color:#7a7a7a">事件类型</span>
          <span>{{item.type}}</span>
        </div>
        <div class="eventDetailsDataList">
          <span style="color:#7a7a7a">事件时间</span>
          <span>{{item.gmtEvent}}</span>
        </div>
        <div class="eventDetailsDataList">
          <span style="color:#7a7a7a">事件地点</span>
          <span>{{item.address}}</span>
        </div>
        <div class="eventDetailsDataList">
          <span style="color:#7a7a7a">接收人</span>
          <span>{{item.receUser}}</span>
        </div>
        <div class="eventDetailsDataList">
          <span style="color:#7a7a7a">事件内容</span>
          <span>{{item.content}}</span>
        </div>
        <div class="eventDetailsDataList">
          <span style="color:#7a7a7a">发起时间</span>
          <span>{{item.gmtCreate}}</span>
        </div>
         <div class="eventDetailsDataList">
          <span style="color:#7a7a7a">发起人</span>
          <span>{{item.createUser}}</span>
        </div>
      </div>
    </div>
    <div class="contanerMain1">
      <!-- <select name="" id=""></select> -->
      <div class="headerContent">
        <!-- <img src="" alt=""> -->
        <mu-container style="width:98%;">
          <mu-expansion-panel
            style="border-radius: 10px;"
            :expand="panel === 'panel2'"
            @change="toggle('panel2')"
          >
            <div slot="header" class="header">现场图片</div>
            <div>
              <div v-for="(item,index) in DataList1" :key="index" class="imgSize">
                <img :src="url+item" alt />
                <!-- <img :src="url+item" alt /> -->
              </div>
            </div>
          </mu-expansion-panel>
          <mu-expansion-panel
            style="border-radius: 10px;"
            :expand="panel === 'panel3'"
            @change="toggle('panel3')"
          >
            <div slot="header" class="header">备注</div>
            <div>
              <div>
                <textarea name="" id="textarea" cols="30" rows="10"></textarea>
              </div>
            </div>
          </mu-expansion-panel>
        </mu-container>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  name: "maintenanceChild3",
  data() {
    return {
      panel: "",
      DataList: [],
      DataList1: [],
      url:"http://47.112.16.168:",
      token:""
      // url:""
    };
  },
  methods: {
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    async initData(){
      var url = "/job/event";
      var name = this.$route.query.name;
      var name121 = "";
      var pageNum = 1;
      var pageSize = 100000000;
      this.token = sessionStorage.getItem("token");
      var res = await AppBace.request(url,"GET",{
        pageNum,pageSize,name,
        // name121
      },this.token);
      this.DataList = res.data.data.rows;
        var photoArr = [];
       $("#textarea").val(res.data.data.rows[0].remark);
      for(let index = 0; index < this.DataList.length; index++) {
       var photo = this.DataList[index].path;
         photoArr = photo.split(",");
         
        this.DataList1= photoArr;
      }
     
      console.log(res);
    }
  },
  mounted() {
    this.initData();
  }
};
</script>
<style scoped>
.contanerMain {
  margin: 0 5px;
  background: white;
  padding: 10px 20px;
  border-radius: 10px;

  margin-top: 10px;
  /* margin-bottom: 50px; */
}
.eventDetailsDataList {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 40px;
  align-items: center;
  border-bottom: 1px solid rgb(238, 236, 236);
}
.header {
  padding-left: 10px;
}
.headerContent {
  margin-top: 10px;
}
.imgSize img{
  width: 100%;
  height: 200px;
}
#textarea{
  width: 100%;
  height: 80px;
  border-radius: 10px;
  font-size: 14px;
  background: rgb(242, 246, 250);
  border: 1px solid #ccc;
  outline: none;
}
</style>



